<script>
import axios from 'axios';
import DefaultConfig from '../../DefaultConfig.vue';
import CryptoJS from 'crypto-js/crypto-js'
import md5 from 'js-md5'


axios.interceptors.response.use(response => {
    return response;
}, error => {
    if (error.response.status === 401) {
        location.hash = '/Hypergryph/Login'
        localStorage.setItem("Cred_info", false);
    }
    return error;
});



const sklan_api_url = 'https://zonai.skland.com';
const bulletins_url = '/api/v1/bulletins/list';
const home_url = '/api/v1/home/index';
const rec_url = '/api/v1/rec/index';



const check = function () {
    axios
        .get("https://zonai.skland.com/api/v1/user/check", {
            headers: {
                "cred": localStorage.getItem('Cred'),
                "platform": "1",
            }
        })
        .then((response) => {
            if (response.data.message == "OK") {
                localStorage.setItem("Cred_info", true);
            }
        }).catch(() => {
        });
}

function bulletins(gameid) {

    axios
        .get("https://zonai.skland.com/api/v1/bulletins/list?gameId=" + gameid + "&cateId=3", {
            headers: {
                "cred": localStorage.getItem('Cred'),
                "platform": "1",
            }
        })
        .then((response) => {
            return response.data
        });
}


var headers_skland = function (url) {


    var time = (new Date().getTime() + '').slice(0, 10);

    var hmstr = {
        "platform": "1",
        "timestamp": time,
        "dId": "639c76b5e872e979",
        "vName": "1.5.1"
    }

    var key = CryptoJS.enc.Hex.stringify(CryptoJS.HmacSHA256(url + time + JSON.stringify(hmstr), localStorage.getItem("Token")));
    var sign = md5(key);

    var headers = {
        "cred": localStorage.getItem('Cred'),
        "platform": "1",
        "timestamp": time,
        "dId": "639c76b5e872e979",
        "vName": "1.5.1",
        "sign": sign
    }

    return headers;
}






// https://zonai.skland.com/api/v1/bulletins/list?gameId=1&cateId=14



